import React from 'react';
import { Avatar, Dropdown, Menu } from 'antd';
import { UserOutlined, LogoutOutlined, EditOutlined, UserAddOutlined, AppstoreOutlined } from '@ant-design/icons';
import { useNavigate } from 'react-router-dom';
import './Header.css';

const Header = ({ userProfile, onLogout }) => {
  const navigate = useNavigate();

  if (!userProfile) return null;

  // Exibir uma mensagem de erro se o Cargo1 estiver indefinido
  if (!userProfile.Cargo1) {
    console.error("Erro: Cargo1 não está definido no userProfile.");
  }

  const handleMenuClick = ({ key }) => {
    if (key === 'editProfile') {
      navigate('/home/editar-perfil');
    } else if (key === 'createUser') {
      navigate('/home/criar-usuario'); // Navega para a página de criar usuário
    } else if (key === 'manageShortcuts') {
      navigate('/home/gerenciar-atalhos'); // Navega para a página de gerenciamento de atalhos
    } else if (key === 'logout') {
      onLogout();
    }
  };

  const menu = (
    <Menu onClick={handleMenuClick}>
      <Menu.Item key="editProfile" icon={<EditOutlined />}>
        Editar Perfil
      </Menu.Item>
      {/* O botão "Criar Usuário" só aparece para Administradores */}
      {userProfile.Cargo1 === 'Administrador' && (
        <Menu.Item key="createUser" icon={<UserAddOutlined />}>
          Criar Usuário
        </Menu.Item>
      )}
        <Menu.Item key="manageShortcuts" icon={<AppstoreOutlined />}>
        Atalhos Pessoais
      </Menu.Item>
      <Menu.Item key="logout" icon={<LogoutOutlined />}>
        Sair
      </Menu.Item>
    </Menu>
  );

  return (
    <div className="header">
      <div className="header-left">
        <div className="user-info">
          <span>{userProfile.name}</span> {/* Exibindo o nome e cargo */}
        </div>
      </div>
      <div className="header-right">
        <Dropdown overlay={menu} placement="bottomRight" trigger={['click']}>
          <Avatar
            size={48}
            src={userProfile.profilePic || null} // Usando a imagem personalizada ou ícone padrão
            icon={!userProfile.profilePic && <UserOutlined />} // Ícone de fallback se não houver imagem
            style={{ cursor: 'pointer' }}
          />
        </Dropdown>
      </div>
    </div>
  );
};

export default Header;
